        <el-checkbox-group v-model="followForm.dangerType">
            <el-checkbox label="" name="dangerType" value="1"></el-checkbox>
            <el-checkbox label="" name="dangerType" value="2"></el-checkbox>
            <el-checkbox label="" name="dangerType" value="3"></el-checkbox>
            <el-checkbox label="" name="dangerType" value="4"</el-checkbox>
            <el-checkbox label="" name="dangerType" value="5"></el-checkbox>
            <el-checkbox label="55" name="dangerType" value="6"></el-checkbox>
        </el-checkbox-group>
        
    </el-form-item>
    <el-form-item>
        <el-input placeholder="" size="small" v-model="followForm.dangerOther">
        </el-input>
    </el-form-item>
    <el-row :gutter="15" class="mod-info">
        <el-col :span="24">
            <h4></h4>
        </el-col>
        <el-col :span="5">
            <el-form-item label="(mmHg)" prop="bloodHigh" >
                <el-input  placeholder="" type="number"  v-model="followForm.bloodHigh"></el-input>
            </el-form-item>
        </el-col>
        <el-col :span="5">
            <el-form-item label="(mmHg)" prop="bloodLow" >
                <el-input  placeholder="" type="number"  v-model="followForm.bloodLow"></el-input>
            </el-form-item>
        </el-col>
        <el-col :span="5" >
            <el-form-item  type="number" label="(kg)" prop="weight">
                <el-input  placeholder="" type="number" v-model="followForm.weight"></el-input>
            </el-form-item>
        </el-col>
        <el-col :span="5">
            <el-form-item label="(cm)" prop="height">
                <el-input  placeholder="" type="number"  v-model="followForm.height"></el-input>
            </el-form-item>
        </el-col>

        <el-col :span="4">
            <el-form-item label="(cm)" prop="waistline">
                <el-input  placeholder="" type="number" v-model="followForm.waistline"></el-input>
            </el-form-item>
        </el-col>
        <el-col :span="24">
            <el-form-item label="" prop="bodyOther">
                <el-input  placeholder=""  v-model="followForm.other"></el-input>
            </el-form-item>
        </el-col>
    </el-row>
    <el-row :gutter="15" class="mod-info">
        <el-col :span="24">
            <h4></h4>
        </el-col>
        <el-col :span="6">
            <el-form-item label="(/)"prop="smoke">
                <el-input  placeholder="" type="number" v-model="followForm.smoke"></el-input>
            </el-form-item>
        </el-col>
        <el-col :span="6">
            <el-form-item label="(/)" prop="wine">
                <el-input  placeholder="" type="number" v-model="followForm.wine"></el-input>
            </el-form-item>
        </el-col>
        <el-col :span="6">
            <el-form-item label="(/)" prop="salt">
                <el-input  placeholder="" type="number" v-model="followForm.salt"></el-input>
            </el-form-item>
        </el-col>
        <el-col :span="6">
            <el-form-item label="(/)" prop="oil">
                <el-input  placeholder="" type="number" v-model="followForm.oil"></el-input>
            </el-form-item>
        </el-col>
        <el-col :span="6">
            <el-form-item label="(/)" prop="sportNum">
                <el-input  placeholder="" type="number" v-model="followForm.sportNum"></el-input>
            </el-form-item>
        </el-col>
        <el-col :span="6">
            <el-form-item label="(/)" prop="sportMinute">
                <el-input  placeholder="" type="number" v-model="followForm.sportMinute"></el-input>
            </el-form-item>
        </el-col>
        <el-col :span="12">
        <el-form-item label="" prop="heartDebug">
            <el-radio-group v-model="followForm.heartDebug">
                <el-radio label="" value="2"></el-radio>
                <el-radio label="" value="1"></el-radio>
                <el-radio label="" value="0"></el-radio>
            </el-radio-group>
            
        </el-form-item>
        </el-col>
    </el-row>
    <el-row :gutter="15" class="mod-info">
        <el-col :span="24">
            <h4></h4>
        </el-col>
