            <el-radio-group v-model="followForm.isBack">
                <el-radio label="" value="1"></el-radio>
                <el-radio label="" value="0"></el-radio>
            </el-radio-group>
           
        </el-form-item>
        </el-col>
        <el-col :span="16">
        <el-form-item label="" prop="backType">
            <el-radio-group v-model="followForm.backType">
                <el-radio label="" value="1"></el-radio>
                <el-radio label="" value="2"></el-radio>
                <el-radio label="" value="3"></el-radio>
            </el-radio-group>
        </el-form-item>
        </el-col>
        <el-col :span="24" >
            <el-form-item label="" prop="backReason">
                <el-input  placeholder="" v-model="followForm.backReason"></el-input>
            </el-form-item>
        </el-col>
    </el-row>
    <el-row :gutter="15" class="mod-info">
        <el-col :span="24">
            <h4></h4>
        </el-col>
        <el-col :span="7" prop="accept">
        <el-form-item label="" v-model="followForm.accept">
            <el-radio-group v-model="followForm.accept">
                <el-radio label="" value="1"></el-radio>
                <el-radio label="" value="0"></el-radio>
            </el-radio-group>
        </el-form-item>
        </el-col>
        <el-col :span="9">
        <el-form-item label="" prop="ctrlType">
            <el-radio-group v-model="followForm.ctrlType">
                <el-radio label="" value="1"></el-radio>
                <el-radio label="" value="0"></el-radio>
            </el-radio-group>
        </el-form-item>
        </el-col>
        <el-col :span="8">
            <el-form-item label="" prop="assistExam">
                <el-input  placeholder="" v-model="followForm.assistExam"></el-input>
            </el-form-item>
        </el-col>
        <el-col :span="12">
        <el-form-item label="" prop="nextDate">
            <el-date-picker type="date" placeholder="" v-model="followForm.nextDate" style="width: 100%;"></el-date-picker>
        </el-form-item>
        </el-col>
        
    </el-row>
    <el-form-item>
        <el-button type="primary" @click="submitForm('followForm')"></el-button>
        <el-button v-on:click="cancelForm()"></el-button>
    </el-form-item>
    </el-form>
</el-card>
</template>
<style lang="scss">
</style>
<script>
    import util from '@/util'
    import is from 'is'
    var userId = '';
    // var dataSource = [];
    export default {
        props:{
            id:String
        },
        data () {
            return {
                followForm:{
                    thisDate:'',
                    followType:'tel',
                    dangerType:[],
                    dangerOther:'',
                    bloodHigh:'',
                    bloodLow:'',
                    weight:'',
                    height:'',
                    waistline:'',
                    bodyOther:'',
                    smoke:0,
                    wine:0,
                    salt:0,
                    oil:0,
                    heartDebug:0,
                    sportNum:0,
                    sportMinute:0,
                    isBack:0,
                    backType:1,
                    backReason:'',
                    accept:1,
                    ctrlType:'1',
                    assistExam:'',
                    nextDate:''
                },
                rules:{
                    thisDate:[
                        { type: 'date',required: true, message: '', trigger: 'blur' }
                    ],
                    followType:[
                        { required: true, message: '', trigger: 'blur' }
                    ],
                    dangerType:[
                        {type: 'array', required: true, message: '', trigger: 'blur' }
                    ],
                    bloodHigh:[
                        { required: true, message: '', trigger: 'blur' }
                    ],
                    bloodLow:[
                        { required: true, message: '', trigger: 'blur' }
                    ],
                    weight:[
                        { required: true,maxlength:3, message: '', trigger: 'blur' }
                    ],
                    height:[
                        { required: true,maxlength:3, message: '', trigger: 'blur' }
                    ],
                    // waistline:[
                    //     { required: true,maxlength:3, message: '', trigger: 'blur' }
                    // ],
                  
                    // smoke:[
                    //     { required: false,maxlength:3, message: '', trigger: 'blur' }
                    // ],
                    // wine:[
                    //     { required: false,maxlength:3, message: '', trigger: 'blur' }
                    // ],
                    // salt:[
                    //     { required: false,maxlength:3, message: '', trigger: 'blur' }
                    // ],
                    // oil:[
                    //     { required: false,maxlength:3, message: '', trigger: 'blur' }
                    // ],
                    // sportNum:[
                    //     { required: false,maxlength:3, message: '', trigger: 'blur' }
                    // ],
                    // sportMinute:[
                    //     { required: false,maxlength:5, message: '', trigger: 'blur' }
                    // ],
                    isBack:[
                        { required: true, message: '', trigger: 'blur' }
                    ],
                    backType:[
                        { required: true, message: '', trigger: 'blur' }
                    ],
                    backReason:[
                        { required: true, message: '', trigger: 'blur' }
                    ],
                    accept:[
                        { required: true, message: '', trigger: 'blur' }
                    ],
                    ctrlType:[
                        { required: true, message: '', trigger: 'blur' }
                    ],
                    assistExam:[
                        { required: false, message: '', trigger: 'blur' }
                    ],
                    nextDate:[
                        {type: 'date', required: true, message: '', trigger: 'blur' }
                    ]
                }
            }
        },
        methods:{
            submitForm(formName) {
                this.$refs[formName].validate((valid) => {
                if (valid) {
                    let params = {doctorid:userId,pantientid:this.id,context:JSON.stringify(this.followForm)}
                    console.log(params);
                    util.postData('Doctors/AddPantientFollow',{AddPantientFollow:params})
                    .then(data => {
                        this.$emit('addRecode',JSON.parse(params.context));
                        this.$refs['followForm'].resetFields();
                        util.toast('');
                    })
                } else {
                    console.log('error submit!!');
                    return false;
                }
                });
            },
            cancelForm () {
                
                this.$emit('cancelForm');
            },
            resetForm(formName) {
                this.$refs[formName].resetFields();
            }
        },
        mounted () {
            userId = JSON.parse(localStorage.getItem(util.localKey.login)).id ;
            // this.$router.replace('/users/list')
        }
    }
</script>


