<template>
    <div class="detail">
        <div class="detail-box">
            <el-card :body-style="{ padding: '15px' }"  class="personal-data">
                <dl>
                    <dt><i class="icon icon-user"><img src="../../../assets/logo.jpg" /></i></dt>
                    <dd>
                        <p><span class="name">{{userInfo.name}}</span><span class="tag tag-high"></span></p>
                        <p class="info"><span>{{userInfo.birthday}}</span><span></span></p>
                        <p class="tags"><span>{{userInfo.name}}</span><span>{{userInfo.mobile}}</span></p>
                        <p>{{userInfo.province}} {{userInfo.city}} {{userInfo.region+' '+userInfo.address}} </p>
                    </dd>
                </dl>
                <div class="doct-button">
                    <el-button type="primary"  @click="editUserInfo=true"></el-button>
                    <el-button type="primary" @click="editing=true"></el-button>
                </div>
            </el-card>
            <formList v-if="editing" v-on:cancelForm="closeEditForm" v-on:addRecode="addRecode"  :id="userInfo.id"></formList>
              <el-dialog custom-class="addContact" title="" size="large" v-model="editUserInfo">
                <dtdialog :data="userInfo" ></dtdialog>
            </el-dialog>
            <el-card :body-style="{ padding: '15px' }" v-if="!editing">
                <h2></h2>
                <div class="mod-info">
                    <h4></h4>
                    <table>
                        <tr>
                            <td width="20%" v-for="danger in userInfo.context.dangerType">{{danger}}</td>
                        
                        </tr>
                        <tr>
                            <td colspan="4">{{ '  '+(userInfo.context.dangerOther || '--')}}</td>
                        </tr>
                    </table>
                </div>
                <div class="mod-info">
                    <h4></h4>
                    <table>
                        <tr>
                            <td width="20%"><p>(mmHg)</p><p>{{userInfo.context.bloodHigh+'/'+userInfo.context.bloodLow}}</p></td>
                            <td width="20%"><p>(kg)</p><p>{{userInfo.context.weight}}</p></td>
                            <td width="20%"><p>(cm)</p><p>{{userInfo.context.height}}</p></td>
                            <td width="20%"><p>(kg/m2)</p><p>{{userInfo.context.bmi}}</p></td>
                            <td width="20%"><p>(cm)</p><p>{{userInfo.context.waistline}}</p></td>
                        </tr>
                        <tr>
                            <td colspan="5"></td>
                        </tr>
                    </table>
                </div>
                <div class="mod-info">
                    <h4></h4>
                    <table>
                        <tr>
                            <td width="20%"><p>(/)</p><p>{{userInfo.context.smoke}}</p></td>
                            <td width="20%"><p>(/)</p><p>{{userInfo.context.wine}}</p></td>
                            <td width="20%"><p>(/)</p><p>{{userInfo.context.salt}}</p></td>
                            <td width="20%"><p>(/)</p><p>{{userInfo.context.oil}}</p></td>
                            <td width="20%"><p>(/)</p><p>{{userInfo.context.sportNum}}</p></td>
                        </tr>
                        <tr>
                            <td width="20%"><p>(/)</p><p>{{userInfo.context.waistline}}</p></td>
                            <td width="20%"><p></p><p>{{userInfo.context.waistline}}</p></td>
                            <td colspan="3"></td>
                        </tr>
                    </table>
                </div>
                <div class="mod-info">
                    <h4></h4>
                    <table>
                        <tr>
                            <td width="20%"><p></p><p>{{userInfo.context.isBack}}</p></td>
                            <td width="20%"><p></p><p>{{userInfo.context.backType}}</p></td>
                            <td colspan="3"><p></p><p>{{userInfo.context.backReason}}</p></td>
                        </tr>
                    </table>
                </div>
                <div class="mod-info">
                    <h4></h4>
                    <table>
                        <tr>
                            <td width="20%"><p></p><p>{{userInfo.context.accept}}</p></td>
                            <td width="20%"><p></p><p>{{userInfo.context.ctrlType}}</p></td>
                            <td colspan="3"><p></p><p>{{userInfo.context.waistline}}</p></td>
                        </tr>
                        <tr>
                            <td colspan="5">
                                <p></p>
                                <p>{{userInfo.context.nextDate | timeFilter}}</p>
                            </td>
                        </tr>
                    </table>
                </div>
            </el-card>
        </div>
        <div class="other-list">
            <h2></h2>
         
            <div class="list-box">
                <el-card :body-style="{ padding: '10px' }"  class="personal-data " :class="{'personal-data-selected':userInfo.id == patient.id}" v-for="(patient, index) in patients">
                    <dl @click="patientClick(patient)">
                        <dt><i class="icon icon-user"><img src="../../../assets/logo.jpg" /></i></dt>
                        <dd>
                            <p><span class="name">{{patient.name}}</span></p>
                            <p class="info"><span>{{patient.sex | sexFilter}}</span><span>67</span></p>
                            <p><span class="tag tag-high"></span></p>
                        </dd>
                    </dl>
                </el-card>
            </div>
        </div>
    </div>
</template>
<script>
    import formList from '@/pages/users/detail/formList.vue';
    import dtdialog from '@/pages/users/list/addDialog.vue';
    import util from '../../../util'
    import is from 'is'
    var userInfo = {};
    // var dataSource = [];
    //    <!--><div class="add-list">
    //             <p><i class="el-icon-plus"></i></p>
    //             <p><span></span></p>
    //         </div><!-->
    export default {
        components:{
            formList,
            dtdialog
        },
        data () {
            return {
                follows:[1,2,3,4,5,6],
                editing:false,
                editUserInfo:false,
                patients:[],
                userInfo:{}
            }
        },
        methods:{
            closeEditForm () {
                this.editing = false;
            },
            addRecode (context) {
                context.bmi = (context.weight/((context.height*context.height)/10000)).toFixed(2)
                this.userInfo.context = context;
                this.editing = false;
            },
            patientClick (patient) {
                console.log(patient);
                this.userInfo = patient;
                if(!this.userInfo.context.thisDate){
                    this.editing = true;
                } else {
                    this.editing = false;
                }
            }
        },
        mounted () {
            let params = this.$route.params;
            let userInfo = params.selectPatient;
            this.patients = params.patients;
            if (userInfo.id) {
                this.userInfo = userInfo;
                if(!this.userInfo.lastfollowupcontext || this.userInfo.edit==true){
                    this.editing = true;
                }
            } else {
                this.$router.go(-1);
            }

        }
    }
</script>
<style lang="scss" scoped>
@import "../../../style/detail.scss";
.personal-data-selected {
     border:solid 1px #4990E2;
    
}
</style>



