<template>
    <div class="loginWrap">
        <div class="login">
            <i class="icon logo"></i>
            <el-form class="login-form">
                <el-form-item class="form-item">
                    <el-input type="tel" placeholder="" v-model="mobile" name="mobile" @change="mobileChange(mobile)" ></el-input>
                </el-form-item>
                <el-form-item class="form-item">
                    <el-input type="password" placeholder="" v-model="code"></el-input>
                </el-form-item>
                <el-form-item class="form-item">
                    <el-button type="primary" size="large" class="btn-block" @click="login()"></el-button>
                </el-form-item>
            </el-form>
            
        </div>
        <div class="footer" >
        2016-2017  | <br>ICP15007132-1
        </div>
    </div>
</template>
<script>
import util from '@/util'
import md5 from 'md5'
    export default {
        data () {
            return {
                mobile:'',
                code:'',
                smsBtnDisabled:true,
                smsText:''
            }
        },
        methods:{
            login() {
                if(validateInput.call(this,1)){
                    var params = {DoctorLogin:{mobile:this.mobile,password:md5(this.code)}};
                    this.axios.post('Doctors/DoctorLogin',params)
                    .then((data) => {
                        if(data.status == 1) {
                            localStorage.setItem(util.localKey.login,JSON.stringify(data.result))
                            this.$router.replace({
                                name: 'main'
                            })
                        } else {
                            this.$message({
                                message:data.result,
                                type: 'error'
                            });
                        }
                    })
                }
            },
            mobileChange (mobile) {
                console.log(mobile);
               if(util.checkPhone(mobile)) {
                   this.smsBtnDisabled = false;
               }
            }
        }
    };
    function validateInput(type) {
        if(!util.checkPhone(this.mobile)){
                this.$message({
                        message:'',
                        type: 'error'
                    });
                    return false;
            }
         if(type ==1 && this.code.length == 0) {
            this.$message({
                        message:'',
                        type: 'error'
                    });
                    return false;
        }
        return true;
    }
</script>
<style lang="scss">
</style>



